.list-container {
  width: 40%;
  left: 1rem;
  right: 1rem;
  position: absolute;
  top: 3rem;
  margin: 0 auto;
}
.list-container-header {
  display: flex;
  background-color: transparent;
  justify-content: space-between;
  align-items: center;
}
.list-container-header h2 {
  color: white;
  letter-spacing: 8px;
  font-weight: bolder;
  font-size: 2rem;
}

.add-item-container {
  display: flex;
  align-items: center;
  background-color: hsl(235, 24%, 19%);
  padding: 1rem 0;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border: 1px solid hsl(233, 14%, 35%);
  border-radius: 5px;
}
.add-item-container div {
  width: 25px;
  height: 25px;

  border: 1px solid gray;
  border-radius: 50%;
  margin-left: 1rem;
}
.add-item-container input {
  margin-left: 1rem;
  outline: none;
  background-color: transparent;
  border: 0;
  font-size: 1rem;
  width: 100%;
  background-color: transparent;
  color: hsl(234, 39%, 85%);
}

/* list-item-container */

.list-item-container {
  background-color: hsl(235, 24%, 19%);
  color: hsl(234, 39%, 85%);
  margin: 0;
  border-radius: 5px;
}
ul {
  transition: all 2s;
}

.checkbox {
  width: 25px;
  height: 25px;
  background-color: transparent;
  border: 1px solid hsl(233, 14%, 35%);
  border-radius: 50%;
  margin-left: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.checkbox:hover {
  cursor: pointer;
  border-width: 1px;
  border-color: hsl(192, 100%, 67%);
  border-style: solid;
}

.list-item-container ul li {
  display: flex;
  align-items: center;
  list-style: none;
  padding: 1rem 0;
  border-bottom: 1px solid hsl(233, 14%, 35%);
}

.list-item-container ul {
  padding-left: 0;
  margin-left: 0;
  border: 1px solid hsl(233, 14%, 35%);
  border-radius: 5px;
}
.checkbox {
  margin-right: 1rem;
}

.sub-details {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sub-details div {
  padding: 0 0.5rem;
}

.details {
  justify-content: space-between;
}
.sub-details {
  font-size: 0.9rem;
  font-weight: 700;
}
.list-item-container ul li:last-child {
  padding: 1.3rem;
}
.sub-details div:hover {
  transform: scale(1.01);
  transition: all 0.1s;
}
@media screen and (max-width: 1194px) {
  .list-container {
    width: 90%;
  }
}
